@import "../../../data/styles.css";

.circle {
	width: 20px;
	height: 20px;
	background: linear-gradient(to top left, #0062bE, #00A2FE);
	border-radius: 50%;
	position: absolute;
	top: 50%;
	left: 50%;
	margin: 0 0 0 0;
	pointer-events: none;
	mix-blend-mode: multiply;
	z-index: 10;
  }